import React from "react";
import PropTypes from "prop-types";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import {
  Group,
  Card,
  Input,
  Footer,
  Link,
  Button,
  Alert,
  Header,
} from "@vkontakte/vkui";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";

import bridge from "@vkontakte/vk-bridge";

import "./Home.css";

const Home = ({ id, go, fetchedUser, setPopout }) => {
  const closePopout = () => {
    setPopout(null);
  };

  const goPost = () => {
    bridge.send("VKWebAppShare", {
      message: `Мой рост в приложении: ${
        document.getElementById("user_height").value
      } см.`,
      link: "https://vk.com/app7168608",
    });
  };

  const getHeight = () => {
    if (!document.getElementById("user_height").value) {
      setPopout(
        <Alert
          actionsLayout="vertical"
          actions={[
            {
              title: "Ок",
              autoclose: true,
              mode: "cancel",
            },
          ]}
          onClose={closePopout}
        >
          <h2>Ошибка! Заполните анкету</h2>
        </Alert>
      );
      return;
    }

    setPopout(
      <Alert
        actionsLayout="vertical"
        actions={[
          {
            title: "Поделится",
            autoclose: true,
            mode: "commerce",
            action: () => goPost(),
          },
          {
            title: "Ок",
            autoclose: true,
            mode: "cancel",
          },
        ]}
        onClose={closePopout}
      >
        <h2>
          Успешно! Ваш рост {document.getElementById("user_height").value} cm
        </h2>
      </Alert>
    );
  };

  return (
    <Panel id={id}>
      <div className="fon">
        <PanelHeader>Определи свой рост</PanelHeader>
        {fetchedUser && (
          <Group title="User Data Fetched with VK Bridge">
            <Cell
              before={
                fetchedUser.photo_200 ? (
                  <Avatar src={fetchedUser.photo_200} />
                ) : null
              }
              description={
                fetchedUser.city && fetchedUser.city.title
                  ? fetchedUser.city.title
                  : ""
              }
            >
              {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
            </Cell>
          </Group>
        )}

        <Group
          title="Get Your Height"
          header={<Header>Заполните анкету</Header>}
        >
          <Card size="l" mode="shadow" style={{ margin: 15, padding: 10 }}>
            <Input
              type="number"
              id="1"
              placeholder="Введите ваш возраст"
              style={{ marginBottom: 10 }}
            />
            <Input
              type="number"
              id="2"
              placeholder="Введите ваш вес"
              style={{ marginBottom: 10 }}
            />
            <Input
              type="number"
              id="user_height"
              placeholder="Введите ваш рост"
            />
            <Button
              onClick={getHeight}
              size="xl"
              mode="commerce"
              style={{ marginTop: 50 }}
            >
              Определить
            </Button>
          </Card>
        </Group>

        <Footer>
          Made by{" "}
          <Link href="https://vk.com/xop_enotov" target="_blank">
            XOP EHOTOB
          </Link>
        </Footer>
      </div>
    </Panel>
  );
};

Home.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Home;
